import wode from '@/static/mbcImg/my/10.png'
import caigou from '@/static/mbcImg/my/11.png'
import yifa from '@/static/mbcImg/my/8.png'
export default {
	data() {
		return {
			wode:wode,
			caigou:caigou,
			yifa:yifa,
			list:[],
		}
	},
	mounted() {

	},
	created() {
		this.buy();
	},
	methods: {
		buy() {
			if (uni.getStorageSync('landRegist')) {
				let landRegistLG = JSON.parse(uni.getStorageSync('landRegist')); // 读取缓存的用户信息
				console.log(landRegistLG.user.id);
				let params = {}; // 请求总数居时 参数为空
				uni.showLoading({ // 展示loading
					title: '加载中'
				});
				uni.request({
					url: this.api2 + '/rest-rp/user/indexCountData/760', //接口地址。
					// data: this.endParams(params),
					method: 'GET',
					header: {
						Authorization: "Bearer " + landRegistLG.token //将token放到请求头中
					},
					success: (response) => {
						uni.hideLoading();
						console.log(response.data);
						this.list = response.data.content
						console.log(this.list)
						// this.$store.commit('setMation', this.List); // 更新vuex
						
					},
					fail: (error) => {
						uni.hideLoading(); // 隐藏 loading
						uni.showToast({
							title: '网络繁忙，请稍后',
							icon: 'none',
							duration: 1000
						});
						console.log(error, '网络繁忙，请稍后');
					}
				});
			}
		},
		gotosell(e){
			if(e === 1){
				console.log('to买家留言')
				uni.navigateTo({
					url: '/modules/myCompany/myBuyeronAlready/my-BuyeronAlready?id='+e,
				});
			}else if(e === 2){
				console.log('已发留言')
				uni.navigateTo({
					url: '/modules/myCompany/myBuyeronAlready/my-BuyeronAlready?id='+e,
				});
			}
		},
		gotosupply(e){
			console.log('to供应订单')
			uni.navigateTo({
				url: '/modules/InstructionsPrice/Order/Order?id='+e,
			});
		},
		gotoPurchase(){
			console.log('to我的采购')
			uni.navigateTo({
				url: '/modules/InstructionsPrice/Purchase/Purchase',
			});
		},
	},
	computed: {
		i18n() {
		  return this.$t('My')  
		},
	},
}
